:root {
  --blue-text: #137791;
  --dark-bg: #090909;
  --orange: #F69333;
  --gray: #3C4A3E;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding-bottom: 2em;
  font-family: 'Sen', sans-serif;
  font-size: 1.2rem;
  line-height: 1.6;
  color: var(--blue-text);
  background-color:var(--dark-bg);
  
}
/* .main {
  display: flex;
  flex-direction: row;
}
.grid {
  display: grid;
  grid-template-rows: repeat(3,1fr);
} */
.logo {
  width: 80px;
  margin: 0.8%;
  border-radius: 10%;
}
.mainDiv {
max-width: 50%;
min-width: 250px;
display: block;
min-height: 35%;
height: 350px;
border-right: 2px solid var(--orange);
/* margin: auto; */
padding: 0;
margin-bottom: 2%;
text-align: center;


}
.mainDiv img {
  max-width: 80%;
  width: 250px;
  min-width: 15%;
  max-height: 65%;
}
.mainDiv-1, .mainDiv-2 {
  margin-bottom: 2%;
  margin-right: 2%;
}
.mainDiv-1 p, .mainDiv-2 p {
  padding: 20px;
  word-break: break-all;
}
.mainDiv-1 {
  border-right: 2px solid var(--orange);
  margin-left: -40px;
}
.mainDiv-2 {
  font-weight: 700;
}
.mainDiv-2 span {
  padding: 20px;
}
.main-title {
  text-align: center;
  font-size: 40px;
  margin: 0 0 10px 0;
}
.div-flex {
  display: inline-grid;
  padding: 5px;
  grid-template-columns: repeat(3,1fr);
  gap: 3px;
  border: 0.5px solid var(--blue-text);
  margin: 5px;
}
/* .div-flex p {
  margin-left: 2%;
} */
.load-more {
  padding: 7px 30px;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  color: #c6f7cc;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 10px;
  background-color: var(--blue-text);
  font-size: 22px;
}
h1, h3 {
  font-weight: 900;
  line-height: 1;
}

h1 {
  font-size: 3rem;
  margin: 0;
}

h2 {
  margin: 0;
  
}

h3 {
  font-size: 2.5rem;
}
h5 {
  color: #c6f7cc;
  letter-spacing: 2px;
}
.links {
  position: fixed;
  left: 50%;
  top: 2%;
  border-radius: 5px;
  border: 5px solid rgba(6,90,112,0.2);
  display: flex;
  transform: translateX(-50%);
  backdrop-filter: blur(4px);
}
.link {
  padding: 3px 35px;
  text-decoration: none;
  font-size: 18px;
  letter-spacing: 1.5px;
  color: #c6f7cc;
}
.input-search {
  padding: 10px 25px;
  border-radius: 5px;
  margin-top: 30px;
  background-color: var(--orange);
  border: none;
}
input::placeholder{
  color: #111111;
  font-weight: bold;
}
.logo {
  color: red;
}
a {
  color: var(--orange);
}

.links a:hover,
.links a:focus {
  color: rgb(159, 185, 192);
  background-color: #3C4A3E;
  border-bottom: 2px solid white;
}
sidebar {
  background: #c6f7cc;
  padding: 1em;
  color:#3C4A3E;
}
sidebar a {
  color: #3C4A3E;
}
sidebar a:hover {
  color: var(--orange);
}
sidebar img {
  max-width: 100%;
  object-fit: cover;
}
header {
  width: 100%;
  background: var(--dark-bg);
  text-align: center;
  padding: 2em 0;
  color: var(--orange);
  border: none;
  outline: none;
  border-bottom: 5px dotted var(--blue-text);
  
}
/* header:hover {
  filter: blur(px);
  opacity: 0.9;
  border: none;
  outline: none;
  border-bottom: 5px dotted transparent;
} */
header a {
  text-decoration: none;
  /* border-left: 1px solid #3C4A3E; */
  padding: 0.2em 1.5em;
}
h1 {
  opacity: 1;
}
.intro {
  width: 100%;

}

.summary {
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
  font-size: .95rem;
  padding: 1em 1.5em;
  color: var(--blue-text);
  background: var(--dark-bg);
  
}
/* .summary:hover,
.summary:focus {
  transform: skew(3deg);
  transition: all transform 300ms;
} */
.main {
  background-color:var(--dark-bg);
  padding: 1rem;
}
.footer {
  background-color: var(--dark-bg);
  padding: 1em;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 70px;
  box-shadow: 1px 1px 1px 2px #c6f7cc,
              -1px -1px -4px 8px var(--orange);
}
.footer p {
  margin-left: 30px;
}
.fa {
  padding: 20px;
  font-size: 40px;
  width: 35px;
  text-align: center;
  text-decoration: none;
  margin-right: 10px;
  color: #c6f7cc;
  text-align: center;
 
}

.fa:hover {
  opacity: 0.7;
}



@media (max-width: 376px) {
  .logo {
    position: absolute;
  left: 5%;
  top: 13.4%;
  width: 50px;
  }
  .input-search {
    position: absolute;
    left: 40%;
  }
  .main {
     max-width: 100%;
     align-items: center;
  }
  .main-title {
    font-size: 30px;
    margin-top: 40px;
  }
  header {
    border-bottom: none;
  }
  .mainDiv {
    max-width: 50%;
    border-bottom: 2px solid var(--orange);
    border-right: none;
  }
  .mainDiv-1 p {
    font-size: 18px;
  }
 .mainDiv-1 {
  margin-left: 0;
 }
  .div-flex {
    display: flex;
    justify-content: space-around;
   flex-wrap: wrap;
   width: 80%;
   margin-left: auto;
   margin-right: auto;
    
  }
  .mainDiv-2 {
    margin-bottom:2%;
    border:2px solid #F69333;
   min-width: 100%;
   font-weight: 700;
  }
  .div-flex img {
    max-width: 70%;
  }
  .div-flex p {
    word-break: break-all;
  }
  .load-more {
    font-size: 18px;
    border-radius: 8px;
    border: 2px solid var(--blue-text);
    background:none;
  }
  .sidebar {
      width: 45%;
      margin-right: auto;
  }
  .fa {
    font-size: 25px;
  }
  .resources {
    text-align: center;
  } 
  .footer {
      display:flex;
      font-size: 18px;
      flex-direction: wrap;
  }
}
